div.carrousel-b {
  padding: 0;
  margin: 20px auto 0;
  height: 160px;
}
div.carrousel-b:after {
  content: "flickity";
  display: none;
}
@media screen and (min-width: 960px) {
  div.carrousel-b {
    margin: 0;
    height: auto;
  }
  div.carrousel-b:after {
    content: "";
  }
}
div.carrousel-b div.carrousel-cell {
  position: relative;
  overflow: auto;
  margin: 0 10px 0;
  height: 100%;
  width: 48%;
  background: #FFF;
  border-radius: 8px;
}
@media screen and (min-width: 768px) {
  div.carrousel-b div.carrousel-cell {
    width: 160px;
  }
}
@media screen and (min-width: 960px) {
  div.carrousel-b div.carrousel-cell {
    margin: 20px auto;
    height: auto;
    width: auto;
  }
  div.carrousel-b div.carrousel-cell:first-of-type {
    margin-top: 0;
  }
}
div.carrousel-b div.carrousel-cell img.thumbnail {
  margin: 14px 14px 8px;
  width: 40px;
}
@media screen and (min-width: 960px) {
  div.carrousel-b div.carrousel-cell img.thumbnail {
    position: absolute;
    top: 50%;
    left: 20px;
    margin: 0;
    width: 60px;
    transform: translateY(-50%);
  }
}
div.carrousel-b div.carrousel-cell h1, div.carrousel-b div.carrousel-cell h2, div.carrousel-b div.carrousel-cell h3, div.carrousel-b div.carrousel-cell h4, div.carrousel-b div.carrousel-cell h5, div.carrousel-b div.carrousel-cell h6 {
  color: #C1C1C1;
  font-family: "Gilroy", sans-serif;
}
div.carrousel-b div.carrousel-cell h1.time, div.carrousel-b div.carrousel-cell h2.time, div.carrousel-b div.carrousel-cell h3.time, div.carrousel-b div.carrousel-cell h4.time, div.carrousel-b div.carrousel-cell h5.time, div.carrousel-b div.carrousel-cell h6.time {
  margin: 0 14px 6px;
  color: #C1C1C1;
}
div.carrousel-b div.carrousel-cell h1.title, div.carrousel-b div.carrousel-cell h2.title, div.carrousel-b div.carrousel-cell h3.title, div.carrousel-b div.carrousel-cell h4.title, div.carrousel-b div.carrousel-cell h5.title, div.carrousel-b div.carrousel-cell h6.title {
  margin: 6px 20px 14px 14px;
  color: #103460;
}
@media screen and (min-width: 960px) {
  div.carrousel-b div.carrousel-cell h1.time, div.carrousel-b div.carrousel-cell h2.time, div.carrousel-b div.carrousel-cell h3.time, div.carrousel-b div.carrousel-cell h4.time, div.carrousel-b div.carrousel-cell h5.time, div.carrousel-b div.carrousel-cell h6.time {
    margin: 30px 20px 2px 100px;
    font-size: 16px;
  }
  div.carrousel-b div.carrousel-cell h1.title, div.carrousel-b div.carrousel-cell h2.title, div.carrousel-b div.carrousel-cell h3.title, div.carrousel-b div.carrousel-cell h4.title, div.carrousel-b div.carrousel-cell h5.title, div.carrousel-b div.carrousel-cell h6.title {
    margin: 2px 20px 30px 100px;
    font-size: 18px;
  }
}

div.dots {
  position: relative;
  margin: 26px auto 0;
  height: 6px;
  width: 6px;
  transform: translateX(-50%);
  border-radius: 50%;
  background: #C1C1C1;
}
div.dots::before, div.dots::after {
  content: "";
  position: absolute;
  top: 0;
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background: #C1C1C1;
}
div.dots::before {
  left: -16px;
}
div.dots::after {
  right: -16px;
}


